<template>
  <div class="full-img-bg text-white-50" v-bind:style="{ 'background-image': `url(${bgSrc}` }">
    <div class="semitransparent-overlay">
      
      <div class="container-fluid ">
        <h1 class="text-center pt-5">
          <a class="text-white" target="_blank" href="https://blog.instab.io/#/guide/instab.io-101">InstaB.io ðŸ¤©</a>
        </h1>
        
        <div class="row text-center my-5 pt-5">
          <div class="col-12 col-sm-4 my-2">
            <button type="button" class="btn btn-primary border" @click="loginFacebook"><h6>Facebook</h6></button>
          </div>
          <div class="col-12 col-sm-4 my-2">
            <button type="button" class="btn btn-danger border" @click="loginGoogle"><h6>G-mail</h6></button>
          </div>
          <div class="col-12 col-sm-4 my-2">
            <button type="button" class="btn btn-info border" @click="loginTwitter"><h6>Twitter</h6></button>
          </div>
        </div>
        
        <div class="text-right m-sm-5 p-sm-5">
          <h3 class="font-weight-light font-italic">"{{ quote }}"</h3>
          <h5>- {{ author }}</h5>
        </div>
      </div>

    </div>
  </div>
</template>

<script language="ts">
import firebase from 'firebase/app'
import { auth } from '../firebase.config'
import { getRandomFromArr } from '../utils/utils'
import bgImages from '@/utils/bgImages'
import allQuotes from '@/utils/quotes'

export default {
  setup() {
    const { author, quotes } = getRandomFromArr(allQuotes) 
    const quote = getRandomFromArr(quotes)
    
    const bgSrc = getRandomFromArr(bgImages)
    const loginFacebook = () => auth.signInWithPopup(new firebase.auth.FacebookAuthProvider())
    const loginGoogle = () => auth.signInWithPopup(new firebase.auth.GoogleAuthProvider())
    const loginTwitter = () => auth.signInWithPopup(new firebase.auth.TwitterAuthProvider())

    return {
      author,
      quote,
      bgSrc,
      loginFacebook,
      loginGoogle,
      loginTwitter,
    }
  }
}
</script>

